// FAQ functionality
document.addEventListener('DOMContentLoaded', function() {
    // Initialize FAQ functionality
    const faqItems = document.querySelectorAll('.faq-item');
    
    faqItems.forEach(item => {
        const question = item.querySelector('.faq-question');
        if (question) {
            question.addEventListener('click', () => {
                // Close all other FAQ items
                faqItems.forEach(otherItem => {
                    if (otherItem !== item && otherItem.classList.contains('active')) {
                        otherItem.classList.remove('active');
                        const otherAnswer = otherItem.querySelector('.faq-answer');
                        if (otherAnswer) {
                            otherAnswer.style.maxHeight = 0;
                        }
                    }
                });
                
                // Toggle current FAQ item
                item.classList.toggle('active');
                const answer = item.querySelector('.faq-answer');
                if (answer) {
                    if (item.classList.contains('active')) {
                        answer.style.maxHeight = answer.scrollHeight + 'px';
                    } else {
                        answer.style.maxHeight = 0;
                    }
                }
            });
        }
    });
    
    // Initialize testimonial slider for mobile
    initTestimonialSlider();
    
    // Add image scroll effects
    addImageScrollEffects();
    
    // Initialize service request buttons
    initServiceRequestButtons();
});

// Ø¥Ø¶Ø§ÙØ© Ø´Ø±ÙŠØ· ØªÙ…Ø±ÙŠØ± Ù„Ù„Ù…Ø±Ø§Ø¬Ø¹Ø§Øª Ø¹Ù„Ù‰ Ø§Ù„Ù‡ÙˆØ§ØªÙ
function initTestimonialSlider() {
    const testimonialsGrid = document.querySelector('.testimonials-grid');
    if (!testimonialsGrid) return;
    
    if (window.innerWidth <= 768) {
        testimonialsGrid.style.display = 'flex';
        testimonialsGrid.style.overflowX = 'auto';
        testimonialsGrid.style.scrollSnapType = 'x mandatory';
        testimonialsGrid.style.gap = '20px';
        testimonialsGrid.style.padding = '10px 0';
        
        // Ø¥Ø¶Ø§ÙØ© Ù†Ù…Ø· Ù„Ù„Ø¨Ø·Ø§Ù‚Ø§Øª ÙÙŠ ÙˆØ¶Ø¹ Ø§Ù„Ø³Ù„Ø§ÙŠØ¯Ø±
        document.querySelectorAll('.testimonial-card').forEach(card => {
            card.style.minWidth = 'calc(100% - 20px)';
            card.style.scrollSnapAlign = 'center';
        });
    } else {
        testimonialsGrid.style.display = 'grid';
        testimonialsGrid.style.overflowX = 'visible';
        testimonialsGrid.style.scrollSnapType = 'none';
        
        // Ø¥Ø²Ø§Ù„Ø© Ø§Ù„Ø£Ù†Ù…Ø§Ø· Ù„Ù„Ø³Ù„Ø§ÙŠØ¯Ø±
        document.querySelectorAll('.testimonial-card').forEach(card => {
            card.style.minWidth = '';
            card.style.scrollSnapAlign = '';
        });
    }
}

// Ø¥Ø¶Ø§ÙØ© ØªØ£Ø«ÙŠØ±Ø§Øª Ù„Ù„ØµÙˆØ± Ø¹Ù†Ø¯ Ø§Ù„ØªÙ…Ø±ÙŠØ±
function addImageScrollEffects() {
    const images = document.querySelectorAll('.image-grid img');
    
    images.forEach(img => {
        const observer = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    entry.target.style.opacity = '1';
                    entry.target.style.transform = 'scale(1)';
                }
            });
        }, { threshold: 0.1 });
        
        img.style.opacity = '0';
        img.style.transform = 'scale(0.95)';
        img.style.transition = 'all 0.5s ease';
        
        observer.observe(img);
    });
}

